function slider({container, slide, nextArrow, prevArrow, total, current, wrapper, field}) {
    // 

    const slides = document.querySelectorAll(slide),
        slider = document.querySelector(container),
        arrowNext = document.querySelector(nextArrow),
        arrowPrev = document.querySelector(prevArrow),
        currentCounter = document.querySelector(current),
        totalCounter = document.querySelector(total),
        sliderWrapper = document.querySelector(wrapper),
        sliderField = document.querySelector(field),
        width = window.getComputedStyle(sliderWrapper).width; //,   

    //    

    let sliderIndex = 1;

    //  1


    // if (slides.length < 10) {
    //     totalCounter.textContent = `0${slides.length}`;
    // }

    // sliderChange(sliderIndex);

    // function sliderChange(id) {
    //     if (id > slides.length) {
    //         sliderIndex = 1;
    //     }

    //     if (id < 1) {
    //         sliderIndex = slides.length;
    //     }
    //     slides.forEach(item => {
    //         item.classList.remove('show');
    //         item.classList.add('hide');
    //     });
    //     slides[sliderIndex- 1].classList.add('show');
    //     if (slides.length < 10) {
    //         currentCounter.textContent = `0${sliderIndex}`;
    //     } else {
    //         currentCounter.textContent = `${sliderIndex}`;
    //     }
    // }

    // function plusSlides(n) {
    //     sliderChange(sliderIndex += n);
    // }

    // arrowPrev.addEventListener('click', () => {
    //     plusSlides(-1);

    // });

    // arrowNext.addEventListener('click', () => {
    //     plusSlides(1);

    // });

    //  2

    //      
    let offset = 0;

    //   
    if (slides.length < 10) {
        totalCounter.textContent = `0${slides.length}`;
        currentCounter.textContent = `0${sliderIndex}`;
    } else {
        totalCounter.textContent = slides.length;
        currentCounter.textContent = sliderIndex;
    }
    //      
    //        
    sliderField.style.width = 100 * slides.length + '%';
    sliderField.style.display = 'flex';
    sliderField.style.transition = '0.5s all';

    //     ,     
    sliderWrapper.style.overflow = 'hidden';

    //   
    slides.forEach(slide => {
        slide.style.width = width;
    });


    slider.style.position = 'relative';

    const indicators = document.createElement('ol'),
        dots = [];
    indicators.classList.add('carousel-indicators');
    indicators.style.cssText = `
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 15;
        display: flex;
        justify-content: center;
        margin-right: 15%;
        margin-left: 15%;
        list-style: none;
    `;
    slider.append(indicators);

    for (let i = 0; i < slides.length; i++) {
        const dot = document.createElement('li');
        dot.setAttribute('data-slide-to', i + 1);
        dot.style.cssText = `
            box-sizing: content-box;
            flex: 0 1 auto;
            width: 30px;
            height: 6px;
            margin-right: 3px;
            margin-left: 3px;
            cursor: pointer;
            background-color: #fff;
            background-clip: padding-box;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            opacity: .5;
            transition: opacity .6s ease;
        `;

        if (i == 0) {
            dot.style.opacity = 1;
        }
        indicators.append(dot);
        dots.push(dot);
    }
    //    
    arrowNext.addEventListener('click', () => {
        //      , 
        // - ,   ofsset  0,   
        // slice       
        //   
        if (offset === +width.slice(0, width.length - 2) * (slides.length - 1)) {
            offset = 0;
        } else {
            //  offset   
            offset += +width.slice(0, width.length - 2);
        }

        //    translate
        sliderField.style.transform = `translateX(-${offset}px)`;

        //  ,        1
        //   
        if (sliderIndex == slides.length) {
            sliderIndex = 1;
        } else {
            sliderIndex++;
        }
        //    10,  0,
        // 
        if (slides.length < 10) {
            currentCounter.textContent = `0${sliderIndex}`;
        } else {
            currentCounter.textContent = sliderIndex;
        }

        dots.forEach(dot => {
            dot.style.opacity = '.5';
        });
        dots[sliderIndex - 1].style.opacity = 1;
    });

    arrowPrev.addEventListener('click', () => {
        //      ,
        //         ,
        //   
        if (offset === 0) {
            offset = +width.slice(0, width.length - 2) * (slides.length - 1);
        } else {
            //  offset   
            offset -= +width.slice(0, width.length - 2);
        }

        sliderField.style.transform = `translateX(-${offset}px)`;


        if (sliderIndex == 1) {
            sliderIndex = slides.length;
        } else {
            sliderIndex--;
        }

        if (slides.length < 10) {
            currentCounter.textContent = `0${sliderIndex}`;
        } else {
            currentCounter.textContent = sliderIndex;
        }

        dots.forEach(dot => {
            dot.style.opacity = '.5';
        });
        dots[sliderIndex - 1].style.opacity = 1;
    });

    dots.forEach(dot => {
        dot.addEventListener('click', (e) => {
            const slideTo = e.target.getAttribute('data-slide-to');

            sliderIndex = slideTo;
            offset = +width.slice(0, width.length - 2) * (slideTo - 1);
            sliderField.style.transform = `translateX(-${offset}px)`;

            if (slides.length < 10) {
                currentCounter.textContent = `0${sliderIndex}`;
            } else {
                currentCounter.textContent = sliderIndex;
            }

            dots.forEach(dot => {
                dot.style.opacity = '.5';
            });
            dots[sliderIndex - 1].style.opacity = 1;
        });
    });

}



export default slider;